import React, { Component } from 'react';

class Contact extends Component {
   constructor(props) {
      super(props);
      this.state = {
         contactName: "",
         contactEmail: "",
         contactSubject: "",
         contactMessage: ""
      }
   }
  render() {

   const { name, city, state, phone, message } = this.props.data ?? {};

   const formAction = "mailto:gurvirdehal@gmail.com?subject=" +
      `${encodeURIComponent(this.state.contactSubject)}&body=${encodeURIComponent(this.state.contactMessage)}`;

   return (
      <section id="contact">

         <div className="row section-head">

            <div className="two columns header-col">

               <h1><span>Get In Touch.</span></h1>

            </div>

            <div className="ten columns">

                  <p className="lead">{message}</p>

            </div>

         </div>

         <div className="row">
            <div className="eight columns">

               <form action= {formAction} method="post" id="contactForm" name="contactForm">
					<fieldset>

                  <div>
						   <label htmlFor="contactName">Name</label>
						   <input type="text" defaultValue="" size="35" id="contactName" name="contactName" onChange={this.handleChange.bind(this)}/>
                  </div>

                  <div>
						   <label htmlFor="contactEmail">Email</label>
						   <input type="text" defaultValue="" size="35" id="contactEmail" name="contactEmail" onChange={this.handleChange.bind(this)}/>
                  </div>

                  <div>
						   <label htmlFor="contactSubject">Subject</label>
						   <input type="text" defaultValue="" size="35" id="contactSubject" name="contactSubject" onChange={this.handleChange.bind(this)}/>
                  </div>

                  <div>
                     <label htmlFor="contactMessage">Message</label>
                     <textarea cols="50" rows="15" id="contactMessage" name="contactMessage" onChange={this.handleChange.bind(this)}></textarea>
                  </div>

                  <div>
                     <button className="submit">Submit</button>
                     <span id="image-loader">
                        <img alt="" src="images/loader.gif" />
                     </span>
                  </div>
					</fieldset>
				   </form>

           <div id="message-warning"> Error boy</div>
				   <div id="message-success">
                  <i className="fa fa-check"></i>Your message was sent, thank you!<br />
				   </div>
           </div>


            <aside className="four columns footer-widgets">
               <div className="widget widget_contact">

					   <h4>Contact Me</h4>
					   <p className="address">
						   {name}<br />
						   {city}, {state}<br />
						   <span>{phone}</span>
					   </p>
				   </div>
            </aside>
      </div>
   </section>
    );
  }
  handleChange(e) {
     this.setState({[e.target.name]: e.target.value});
  }
}

export default Contact;
